import React, { useState, useEffect } from "react";
import Header from "../Header";
import styled, { ThemeProvider } from "styled-components";
import Modal from "../Modal";
import ContactForm from "../ContactForm";
import { device } from "@layouts/styleConfig";
import { lightTheme, darkTheme } from "@layouts/theme";

const Layout = styled.div.withConfig({ componentId: "Container_Layout" })`
  background-color: ${({ theme }) => theme.bgColor};
  color: ${({ theme }) => theme.color};
  transition: background 0.2s ease-in, color 0.2s ease-in;

  .main-body {
    min-height: calc(100vh - 40px);
    box-sizing: border-box;

    .flexCenter_layout {
      min-height: calc(100vh - 40px);
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  @media ${device.tablet} {
    .main-body {
      min-height: calc(100vh - 70px);

      .flexCenter_layout {
        min-height: calc(100vh - 70px);
      }
    }
  }
`;

type themeType = "dark" | "light";

type ContainerPropTypes = {
  children: React.ReactNode;
  location?: { pathname: string };
};

export default function Container({ children, location }: ContainerPropTypes) {
  const [open, setOpen] = useState(false);
  const toggleOpen = () => setOpen((t) => !t);

  const [theme, setTheme] = useState("dark");
  const isDarkTheme = theme === "dark";
  const toggleTheme = (type: themeType | null) => {
    if (!type) {
      setTheme(isDarkTheme ? "light" : "dark");
      return;
    }
    setTheme(type);
  };
  const currentPath = location?.pathname ?? "";

  useEffect(() => {
    const html = document.querySelector("html") as HTMLElement;
    open ? (html.style.overflow = "hidden") : (html.style.overflow = "visible");
  }, [open]);

  return (
    <ThemeProvider theme={isDarkTheme ? darkTheme : lightTheme}>
      <Layout>
        <Header
          toggleModal={toggleOpen}
          toggleTheme={toggleTheme}
          path={currentPath}
        />
        <div className="main-body">{children}</div>
        <Modal
          open={open}
          toggleOpen={() => toggleOpen()}
          render={<ContactForm onCancel={toggleOpen} />}
        />
      </Layout>
    </ThemeProvider>
  );
}
